.checkbox {
  background: #fff;
  border-radius: 5px;
}
.checkbox::before {
  position: absolute;
  content: "";
  width: 20px;
  height: 20px;
  opacity: 0;
  transition: 0.2s;
}
.checkbox__input {
  display: none;
}
.checkbox__input:checked + .checkbox__checked .icon-angle-down {
  scale: 1;
  opacity: 1;
}
.checkbox__checked {
  width: 20px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.checkbox__checked .icon-angle-down {
  color: #2b3769;
  font-size: 24px;
  transition: scale 0.2s ease-in-out;
  scale: 0;
}

